import { useState, useEffect } from "react";
import Player from "./components/Player/Player";

function App() {
  const [songs] = useState([
    {
      title: "Forget me too ft. Halsey",
      artist: "Machine Gun Kelly",
      img_src: "./images/music.jpg",
      src: "./music/on-n-on.mp3",
    },
    {
      title: "Away",
      artist: "Oxlade",
      img_src: "./images/music.jpg",
      src: "./music/away.mp3",
    },
    {
      title: "African-Giant",
      artist: "Burna Boy",
      img_src: "./images/music.jpg",
      src: "./music/african-giant.mp3",
    },
    {
      title: "Best Life",
      artist: "Cardi B ft Chance the Rapper",
      img_src: "./images/music.jpg",
      src: "./music/bestlife.m4a",
    },
    {
      title: "But A Dream",
      artist: "G-Easy",
      img_src: "./images/music.jpg",
      src: "./music/butadream.m4a",
    },
    {
      title: "Find My Way",
      artist: "Dababy",
      img_src: "./images/music.jpg",
      src: "./music/findmyway.m4a",
    },
    {
      title: "God's Plan",
      artist: "Drake",
      img_src: "./images/music.jpg",
      src: "./music/godsplan.m4a",
    },
    {
      title: "Pressure",
      artist: "Jackboy",
      img_src: "./images/music.jpg",
      src: "./music/pressure.m4a",
    },
    {
      title: "Gbona",
      artist: "Burna Boy",
      img_src: "./images/music.jpg",
      src: "./music/gbona.mp3",
    },
  ]);

  const [currentSongIndex, setCurrentSongIndex] = useState(0);
  const [nextSongIndex, setNextSongIndex] = useState(0);

  useEffect(() => {
    setNextSongIndex(() => {
      if (currentSongIndex + 1 > songs.length - 1) {
        return 0;
      } else {
        return currentSongIndex + 1;
      }
    });
  }, [currentSongIndex]);

  return (
    <div className="App">
      <Player
        currentSongIndex={currentSongIndex}
        setCurrentSongIndex={setCurrentSongIndex}
        nextSongIndex={nextSongIndex}
        songs={songs}
      />
    </div>
  );
}

export default App;
